.main-button {
  border-radius: 12px;
  background: linear-gradient(270deg, #8743ff 0%, #4136f1 100%);
  box-shadow: 0px 15px 30px 0px rgba(20, 102, 204, 0.16);
  color: #fff;
  font-size: 12px;
  font-weight: 700;
  padding: 10px 19px;
  border: none;
  transition: all 0.5s;
  cursor: pointer;
  &.primary {
    padding: 12px 31px;
    font-size: 14px;
  }
  &:hover {
  transform: translateY(3px);
  }
}
